import React from "react";
import { toast } from "react-toastify";

let toastId = null;

const toastOptions = {
  autoClose: 5000,
  hideProgressBar: true,
  pauseOnHover: true,
  className: "new alert"
};

//autoClose?: number | false;
//closeOnClick?: boolean;
//closeButton?: React.ReactNode | false;

const toastOptionsLongClose    = { ...toastOptions, autoClose: 10000 }; //10 secs!
const toastOptionsTooLongClose = { ...toastOptions, autoClose: 30000 }; //30 secs!
const toastOptionsNotClose     = { ...toastOptions, autoClose: false }; //TODO? closeOnClick: true


const InfoHtml = ({ msg, title }) =>
  (<div>
    <span className="toastify-content--icon toastify-content--info-icon">
      <i className="fa fa-exclamation-triangle"></i>
    </span>
    <div className="toastify-content--message toastify-content--info-message">
      <strong>{title}</strong>
      <div className="toast-message">{msg}</div>
    </div>
  </div>);


//export const info = (msg, title) => {
export const info = (msg, title, options) => {
    if (toast.isActive(toastId))
    return;

  //title = (title) ? title : 'Information for you!';
  if (!title)
    title = "Bilgi"; //TODO? CARE SELECTED_LANGUAGE!

  if (!options)
    options = toastOptions;

  toastId = toast.info(<InfoHtml msg={msg} title={title}/>, options);
};


export const infoLong = (msg, title) => {
  info(msg, title, toastOptionsLongClose);
};

export const infoTooLong = (msg, title) => {
  info(msg, title, toastOptionsTooLongClose);
};

export const infoPause = (msg, title) => {
  info(msg, title, toastOptionsNotClose);
};


//----------------------------------
const SuccessHtml = ({ msg, title }) =>
  (<div>
    <span className="toastify-content--icon toastify-content--success-icon">
      <i className="fa fa-thumbs-up"></i>
    </span>
    <div className="toastify-content--message toastify-content--success-message">
      <strong>{title}</strong>
      <div className="toast-message">{msg}</div>
    </div>
  </div>);

export const success = (msg, title, options) => {
  if (toast.isActive(toastId))
    return;

  //title = (title) ? title : 'Well done!';
  if (!title)
    title = "Başarılı";

  if (!options)
    options = toastOptions;

  toastId = toast.success(<SuccessHtml msg={msg} title={title}/>, options);
};

export const successLong = (msg, title) => {
  success(msg, title, toastOptionsLongClose);
};

export const successTooLong = (msg, title) => {
  success(msg, title, toastOptionsTooLongClose);
};

export const successPause = (msg, title) => {
  success(msg, title, toastOptionsNotClose);
};


//----------------------------------
const WarnHtml = ({ msg, title }) =>
  (<div>
    <span className="toastify-content--icon toastify-content--warning-icon">
      <i className="fa fa-exclamation-triangle"></i>
    </span>
    <div className="toastify-content--message toastify-content--warning-message">
      <strong>{title}</strong>
      <div className="toast-message">{msg}</div>
    </div>
  </div>);

export const warn = (msg, title, options) => {
  if (toast.isActive(toastId))
    return;

  if (!title)
    title = "Uyarı!";

  if (!options)
    options = toastOptions;

  toastId = toast.warn(<WarnHtml msg={msg} title={title}/>, options);
};

export const warnLong = (msg, title) => {
  warn(msg, title, toastOptionsLongClose);
};

export const warnTooLong = (msg, title) => {
  warn(msg, title, toastOptionsTooLongClose);
};

export const warnPause = (msg, title) => {
  warn(msg, title, toastOptionsNotClose);
};


