const ErrorHtml = ({ msg, title }) =>
  (<div>
    <span className="toastify-content--icon toastify-content--error-icon">
      <i className="fa fa-bug"></i>
    </span>
    <div className="toastify-content--message toastify-content--error-message">
      <strong>{title}</strong>
      <div className="toast-message">{msg}</div>
    </div>
  </div>);

//export const error = (msg, title) => {
export const error = (msg, title, options) => {
  if (toast.isActive(toastId))
    return;

  //title = (title) ? title : 'Oh snap!';
  if (!title)
    title = "Hata!";

  if (!options)
    options = toastOptions;

  toastId = toast.error(<ErrorHtml msg={msg} title={title}/>, options);
};


export const errorLong = (msg, title) => {
  error(msg, title, toastOptionsLongClose);
};

export const errorTooLong = (msg, title) => {
  error(msg, title, toastOptionsTooLongClose);
};

export const errorPause = (msg, title) => {
  error(msg, title, toastOptionsNotClose);
};
