import { HOME, HOME_INDEX, ABOUTUS, BLOG, SERVICE_PFX, CONTACT } from "../util/Config";

import "../asset/scss/main.scss";

import { debugEnabled, consoleLogPrefixMsg } from "../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("! Dashboard");
const consoleLog = (msg) => consoleLogMsg(msg);

class Dashboard extends Component {

    constructor(props, context) {
        super(props, context);

        this._canMount = true;
        if (debugEnabled)
            consoleLog("constructor.props:" + (props ? "+EXIST" : "!ABSENT") + ", context:" + (context ? "+EXIST" : "!ABSENT"));

        const link = this.fixWindowLocation();
        this.state = {
            pageLink: link
        };
    }

    fixWindowLocation = () => {
        // if (debugEnabled)
        //    consoleLog("fixWindowLocation");
    
        const wloc = (window == null ? null : window.location);
        consoleLog("wloc:" + wloc);
    
        //const ploc = props.location;
        // if (debugEnabled)
        //   consoleLog("ploc:" + ploc);
    
        const wlpathname = (wloc == null ? null : wloc.pathname);
        // if (debugEnabled)
        //   consoleLog("wlpathname:" + wlpathname);
    
        if (debugEnabled) {
            const wlsearch = (wloc == null ? null : wloc.search);
            consoleLog("wlsearch:" + wlsearch);
        }

        return wlpathname;
    }
  

    gotoLink = (link) => {
       if (debugEnabled)
          consoleLog("gotoLink.link: " + link);

       //TODO? After onclick prb on refresh win.loc!
       //const linkWin = this.fixWindowLocation();
       // if (debugEnabled)
       //   consoleLog("->fixWindowLocation: " + linkWin);

       //window.location = link; //force to go to link seperately!
       //this.props.history.push(link);

       this.setState({ pageLink: link });
    }


    componentDidMount() {
        if (debugEnabled)
            consoleLog("componentDidMount._canMount:" + this._canMount);

        //TODO? navigator.onLink

        //TODO?
        //if (this._canMount)
        //    fetchAppVersion(this.responseAppVersion);
    }

    componentWillUnmount() {
        this._canMount = false;
        if (debugEnabled)
            consoleLog("componentWillUnmount_canMount:" + this._canMount);
    }

    /*
    NEW
    LAYOUT; XL(1200+; NotebookStd); { Left:2, Center:9, Right:1? }, Total:12
            LG(992+); { Left:2, Center:9, Right:1? }
            MD(768+); { Left:2, Center:10, Right:- }
            SM(768-); { Left:12, Center:12, Right:- }
    */
    render() {
        const pageLink = this.state.pageLink;
        if (debugEnabled)
            consoleLog("render; canMount:" + this._canMount + ", pageLink:" + pageLink);

        //TODO? Use a function by returning OBJ!
        const paramSideBarLeft = {};

        let GoContent; //First char must be UPPERCASE for dif HTML tag!
        let paramContent = null;

        //TODO!!! Also CARE SideBarLeft
        //TODO!!! Also CARE SideBarRight

        if (pageLink === HOME || pageLink === HOME_INDEX) {
            //paramSideBarLeft.main = pageLink;
            paramSideBarLeft.title = "NO_SEARCH";
            //...
            GoContent = MainPage;
        }
        else
        //TODO? if (pageLink === USERGUIDE || pageLink === ("/#" + USERGUIDE)) {
        if (pageLink === ABOUTUS) {
            //paramSideBarLeft.main = pageLink;
            paramSideBarLeft.title = "ABOUTUS";
            GoContent = AboutUs;
        }
        else
        if (pageLink === BLOG) {
            paramSideBarLeft.title = "BLOG_NEWS";
            GoContent = BlogNews;
        }
        else
        if (pageLink.startsWith(SERVICE_PFX)) {
            paramSideBarLeft.title  = "SERVICE";
            GoContent = Service;
            const serviceArea = pageLink.substring(SERVICE_PFX.length);

            if (debugEnabled)
                consoleLog("paramContent.serviceArea: " + serviceArea);

            paramContent = { area: serviceArea };
        }
        else
        if (pageLink === CONTACT) {
            //paramSideBarLeft.title NONE
            GoContent = Contact;
        }
        else {
            GoContent = NotFound;
        }

        //TODO? row style={{ minHeight: calc(100vh - 80px)}}

        // !!! CARE !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
        // NavBarTop.outerHeight; height:148, marginTopBottom:0, ohTotal:148
        const styleMTop = { marginTop:"153px" }; //Default px
        // !!! CARE !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!

        //const styleSidesBG = { backgroundColor: "lightgoldenrodyellow" };
        const styleSidesBG = { backgroundColor: "lightgray" };
        //alternatives: lightcyan, lightgray, lightgoldenrodyellow, lightslategray, lightskyblue, lightyellow

        //TODO? role="main"

        return (
           <BrowserRouter>
            <div>
                <NavBarTop props={this.props} gotoLink={this.gotoLink}/>
                { /*styleMTop:150px USED for FIXED_NAVBAR */}

                <div className="container-fluid" style={styleMTop}>
                    <div className="row flex-xl-nowrap min-vh-100">
                        <div id="sidebar-left" className="col-12 col-md-2 col-lg-2 col-xl-2" style={styleSidesBG}>
                            <SideBarLeft param={paramSideBarLeft}/>
                        </div>

                        <main id="main-middle" role="main" className="col-12 col-md-10 col-lg-9 col-xl-9">
                            <GoContent param={paramContent}/>
                        </main>

                        <div id="sidebar-right" className="d-none d-lg-block col-lg-1 d-xl-block col-xl-1" style={styleSidesBG}>
                        </div>
                    </div>
                </div>

                <NavBarBottom/>
            </div>
           </BrowserRouter>
        );
    }
}

export default Dashboard;
